<template>
	<div class="winningPublicity">
		<div class="winningPublicity_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="winningPublicity_title">中奖公示</p>
		</div>
		<div class="winningPublicity_main">
			<ul class="goods_list">
				<li @click="towinningPublicityDetails(item)" v-for="(item,index) in foodsList" :key="index">
					<img :src="item.commodityMainImg" alt="" class="img_goods">
					<div class="goods_list_box">
						<p class="text1">
							<img :src="item.logo" alt="" class="icon_logo">
							<span>{{ item.commodityName }}</span>
						</p>
						<p class="text2">
							<i class="icon_time"></i>
							<span>开奖时间：{{ item.endTime }}</span>
						</p>
						<p class="text3">
							<i class="icon_gift"></i>
							<span>共{{ item.quantity }}份奖品</span>
							<i class="icon_heat"></i>
							<span>已参与{{ item.cycleTotalInvestmentCount }}次</span>
						</p>
						<p class="goods_list_btn"></p>
					</div>
					<p class="goods_list_stage">第{{ item.cycle }}期</p>
				</li>
			</ul>
			<p class="winningPublicity_main_tips">只能看到最近7天的开奖记录哦~!</p>
		</div>
	</div>
</template>

<script>
	export default{
		name:'winningPublicity',
		data(){
			return{
				obj:{},
				item:{},
				foodsList:[]
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			if(_this.$route.query.item){
				_this.item = _this.$route.query.item;
			}
			_this.previousPeriodList();
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			towinningPublicityDetails(item){
				this.$router.push({path:'/freeAccess/winningPublicityDetails',query:{item:item}})
			},
			previousPeriodList(){
				let _this = this;
				var requestData = Object.assign(_this.obj,_this.item);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/previousPeriodList',
					data:redata,
				}).then(function(res){
					console.log('previousPeriodList=====================',res);
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log('previousPeriodList=====================',reData);
						for(var i in reData){
							reData[i].endTime = _this.$timestampTpDate(reData[i].endTime,'ymdhms');
						}
						_this.foodsList = reData;
					}
				})
			}
		}
		
	}
</script>

